body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #022F40, #38AECC);
  font-family: Arial, sans-serif;
  margin: 0;
}

.container {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}

.calculator {
  width: 300px;
  padding: 20px;
  background: #2c3e50;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  text-align: center;
  color: white;
}

.display {
  width: 100%;
  height: 50px;
  font-size: 24px;
  text-align: right;
  margin-bottom: 10px;
  padding: 5px;
  border: 2px solid #16a085;
  border-radius: 5px;
  background-color: #34495e;
  color: white;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 8px;
}

button {
  padding: 15px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #16a085;
  color: white;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

button:hover {
  background-color: #1abc9c;
  transform: scale(1.05);
}

button:nth-child(4n),
button:nth-last-child(3),
button:nth-last-child(2) {
  background-color: #e74c3c;
}

button:nth-child(4n):hover,
button:nth-last-child(3):hover,
button:nth-last-child(2):hover {
  background-color: #c0392b;
}

button:nth-last-child(3) {
  background-color: #f39c12;
}

button:nth-last-child(3):hover {
  background-color: #e67e22;
}

button:nth-last-child(2) {
  background-color: #2ecc71;
}

button:nth-last-child(2):hover {
  background-color: #27ae60;
}

.history {
  background: #34495e;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 300px;
  text-align: left;
  color: white;
}

.history h3 {
  margin-bottom: 10px;
  color: #1abc9c;
  text-align: center;
}

.history ul {
  list-style: none;
  padding: 0;
}

.history li {
  padding: 5px;
  font-size: 16px;
  border-bottom: 1px solid #ddd;
}

.history li:last-child {
  border-bottom: none;
}
